//basic
$path: '../../';

// base width
$contentWidth: 1024px;
$maxContentWidth: 1200px;
$spContentWidth: 320px;
$rwdBreakPoint01: 768px;
$rwdBreakPoint02: 980px;

// base color
$baseColorType01: #000000; //base font color
$baseColorType02: #004ba5; //link color
$baseColorType03: #02a0e9; //point color
$placeholderColor: #cccccc; //placeholder color

// base font
$fontFamily: (
  'default': '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Hiragino Sans", "Noto Sans CJK JP", "Original Yu Gothic", "Yu Gothic", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Sans Emoji"',
  'yugo': '"Original Yu Gothic", "Yu Gothic Medium", "游ゴシック Medium", YuGothic, "游ゴシック体", Verdana, Roboto, "Segoe UI", sans-serif',
  'noto': '"Noto Serif JP", serif',
  'roboto': 'Roboto, sans-serif'
);
$fontFamilySet: 'default';

$fontSize: (
  pc: 16,
  sp: 14
);
// map-get($fontSize, 'pc');
$lineHeight: (
  pc: 24,
  sp: 20
);

$fontWeightNormal: 400;

// breakpoints
$breakpoints: (
  sp: 'screen and (max-width: #{$rwdBreakPoint01 - 1px})',
  tab: 'screen and (max-width: #{$rwdBreakPoint02})',
  pc: '(min-width: #{$rwdBreakPoint01})',
  full: '(min-width: #{$contentWidth + 1px})'
);
